import React from "react";
import Header from "../component/Header";

const RegistrationPage = () => {
  return (
    <div> 
    <div  className="header" style={{ position: 'sticky', top: 0, zIndex: 1000 }}>
        <Header/>
        </div>
    <div className="container my-5">
      
      <div className="border rounded shadow-lg bg-white p-4">
        <div className="row align-items-stretch">
          {/* Left Side: Image */}
          <div className="col-md-6 d-flex p-0">
            <img
              src="https://media.vietq.vn/files/HanHien/2023/05/03/logistic.jpg"
              alt="Logo"
              className="img-fluid w-100 rounded-start"
              style={{
                objectFit: "cover", 
                height: "100%", 
                maxHeight: "600px", // Gi chiu cao ti a
                minHeight: "100%", // Gi kch thc ng b vi form
              }}
            />
          </div>

          {/* Right Side: Form */}
          <div className="col-md-6">
            <div className="p-4 h-100">
              <h2 className="text-center font-weight-bold mb-4">ng k</h2>
              <form>
                {/* H */}
                <div className="mb-3">
                  <label htmlFor="firstName" className="form-label">
                    H ca bn <span className="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    id="firstName"
                    className="form-control"
                    placeholder="H"
                  />
                </div>

                {/* Tn */}
                <div className="mb-3">
                  <label htmlFor="lastName" className="form-label">
                    Tn ca bn <span className="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    id="lastName"
                    className="form-control"
                    placeholder="Tn"
                  />
                </div>

                {/* a ch */}
                <div className="mb-3">
                  <label htmlFor="address" className="form-label">
                    a ch <span className="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    id="address"
                    className="form-control"
                    placeholder="a ch"
                  />
                </div>

                {/* S in thoi */}
                <div className="mb-3">
                  <label htmlFor="phone" className="form-label">
                    S in thoi <span className="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    id="phone"
                    className="form-control"
                    placeholder="S in thoi"
                  />
                </div>

                {/* Email */}
                <div className="mb-3">
                  <label htmlFor="email" className="form-label">
                    Email <span className="text-danger">*</span>
                  </label>
                  <input
                    type="email"
                    id="email"
                    className="form-control"
                    placeholder="Email"
                  />
                </div>

                {/* Ngy sinh */}
                <div className="mb-3">
                  <label htmlFor="birthday" className="form-label">
                    Ngy sinh <span className="text-danger">*</span>
                  </label>
                  <input type="date" id="birthday" className="form-control" />
                </div>

                {/* Gii tnh */}
                <div className="mb-3">
                  <label htmlFor="gender" className="form-label">
                    Gii tnh <span className="text-danger">*</span>
                  </label>
                  <select id="gender" className="form-select">
                    <option value="1">Nam</option>
                    <option value="2">N</option>
                  </select>
                </div>

                {/* Tn ng nhp */}
                <div className="mb-3">
                  <label htmlFor="username" className="form-label">
                    Tn ng nhp <span className="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    id="username"
                    className="form-control"
                    placeholder="Tn ng nhp"
                  />
                </div>

                {/* Mt khu */}
                <div className="mb-3">
                  <label htmlFor="password" className="form-label">
                    Mt khu <span className="text-danger">*</span>
                  </label>
                  <input
                    type="password"
                    id="password"
                    className="form-control"
                    placeholder="Mt khu ng nhp"
                  />
                </div>

                {/* Xc nhn mt khu */}
                <div className="mb-3">
                  <label htmlFor="confirmPassword" className="form-label">
                    Xc nhn mt khu <span className="text-danger">*</span>
                  </label>
                  <input
                    type="password"
                    id="confirmPassword"
                    className="form-control"
                    placeholder="Xc nhn mt khu"
                  />
                </div>

                {/* Submit Button */}
                <div className="text-center">
                  <button type="submit" className="btn btn-primary w-100">
                    ng k
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    </div>
  );
};

export default RegistrationPage;
