import React, { useState, useEffect } from "react";
import "../css/app.css"; // Import file CSS
import { useNavigate } from "react-router-dom"; // Hook  chuyn hng
import "bootstrap/dist/css/bootstrap.min.css";
import SearchForm from "../component/SearchForm";
import Services from "../component/Services";
import Footer from "../component/Footer";
import RegistrationForm from "../component/RegistrationForm";
import Header from "../component/Header";

const Home = () => {
  const [products, setProducts] = useState([]); // State  lu tr sn phm
  const [loading, setLoading] = useState(false); // State  theo di trng thi ti d liu
  const [isScrolled, setIsScrolled] = useState(false); // State  kim tra trng thi cun
  const [currentPage, setCurrentPage] = useState(0); // Trang hin ti
  const [totalPages, setTotalPages] = useState(1); // Tng s trang
  const navigate = useNavigate(); // Hook  chuyn hng n trang chi tit
  const [heroImage, setHeroImage] = useState(""); // State lu nh hero section

  const [heroTitle, setHeroTitle] = useState(""); // State lu tiu 
  const [heroDescription, setHeroDescription] = useState(""); // State lu m t

  // Hm fetch banner t API
 // Hm preload nh
const preloadImage = (src) =>
  new Promise((resolve, reject) => {
    const img = new Image();
    img.src = src;
    img.onload = resolve; // nh ti xong
    img.onerror = reject; // Bo li nu khng ti c
  });

// Hm fetch banner t API v preload nh
const fetchBanners = async () => {
  try {
    const response = await fetch("http://14.225.29.33/api/banner/all");
    if (response.ok) {
      const data = await response.json();
      if (data.length > 0) {
        await preloadImage(data[0].imageUrl); // Preload nh t URL
        setHeroImage(data[0].imageUrl); // Ch set URL khi nh ti xong
        setHeroTitle(data[0].title);
        setHeroDescription(data[0].description);
      }
    } else {
      console.error("Failed to fetch banners:", response.statusText);
    }
  } catch (error) {
    console.error("Error fetching banners:", error);
  }
};

  // Gi fetchBanners khi component c mount
  useEffect(() => {
    fetchBanners();
  }, []);

  // Lng nghe s kin cun  thay i trng thi header
  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 100); // Nu cun qu 100px, thay i trng thi
    };

    window.addEventListener("scroll", handleScroll); // Thm s kin cun
    return () => {
      window.removeEventListener("scroll", handleScroll); // Cleanup s kin cun
    };
  }, []);

  // Chuyn trang
  const handlePageChange = (newPage) => {
    if (newPage >= 0 && newPage < totalPages) {
      setCurrentPage(newPage); // Cp nht trang hin ti
    }
  };

  const handleButtonClick = () => {
    navigate("/search"); // Chuyn hng n ng dn
  };

  return (
    <div>

<div  className="header" style={{ position: 'sticky', top: 0, zIndex: 1000 }}>
    <Header/>
    </div>
    <div className={`container-fluid hero-header ${isScrolled ? "scrolled" : ""}`}>
      {/* Hero Section */}
      <section className="hero-section text-center py-5 bg-primary text-white"
       style={{
        backgroundImage: `url(${heroImage})`, // t nh nn t API
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundAttachment: "fixed",
        backgroundRepeat: "no-repeat",
        width: "100%",
        height: "110vh",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        color: "white",
        textShadow: "0 2px 4px rgba(0, 0, 0, 0.7)",
        marginTop: "-5px",
      }}
      
      >
        <div className="container">
        <h1 className="display-4 fw-bold">{heroTitle || "Welcome to ZTO"}</h1>
        <p className="lead">
          {heroDescription || "Cung cp gii php Logistics - Fulfillment ton din."}
        </p>
          
        </div>
      </section>
      <SearchForm/>
      {/* Product Grid Title */}
      <section className="product-grid-title py-4 bg-light mt-2">
        <div className="container">
          <h2 className="text-center fw-bold">Dch v</h2>
        </div>
      </section>

      {/* Product Grid */}
      <section id="products" className="product-grid py-4">
        <div className="container">
         <Services/>
        </div>
      </section>
      
      <div className="bg-light py-3">  <RegistrationForm/></div>
      
      <Footer/>
    </div>
    </div>
  );
};

export default Home;
