import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'font-awesome/css/font-awesome.min.css';
import '../css/SearchForm.css'
const SearchForm = () => {
  const [activeTab, setActiveTab] = useState('search'); // Tab hin ti

  // X l chuyn i gia cc tab
  const handleTabClick = (tab) => {
    setActiveTab(tab);
  };

  // X l tm kim sn phm
  const handleSearchProduct = () => {
    alert('Tm kim sn phm');
  };

  // X l tra cu m vn n
  const handleTrackOrder = () => {
    alert('Tra cu m vn n');
  };

  return (
    <div className="container py-4 bg-light border border-primary rounded shadow">
      {/* Tab Navigation */}
      <div className="fil-top">
        <ul className="nav nav-tabs border-bottom-0">
          <li className="nav-item">
            <a
              className={`nav-link ${activeTab === 'search' ? 'active' : ''}`}
              onClick={() => handleTabClick('search')}
              role="button"
            >
              <i className="fa fa-search"></i> Tm kim sn phm
            </a>
          </li>
          <li className="nav-item">
            <a
              className={`nav-link ${activeTab === 'tracking' ? 'active' : ''}`}
              onClick={() => handleTabClick('tracking')}
              role="button"
            >
              <i className="fa fa-truck"></i> Tra cu m vn n
            </a>
          </li>
        </ul>
      </div>

      {/* Tab Content */}
      <div className="fil-body mt-4">
        {/* Tm kim sn phm */}
        {activeTab === 'search' && (
          <div className="fil-panel">
            <div className="fil-form">
              <div className="d-flex gap-3 mb-3">
                <select className="form-select border-2" aria-label="Chn trang web">
                  <option value="taobao" selected>
                    TAOBAO
                  </option>
                  <option value="tmall">TMALL</option>
                  <option value="1688">1688</option>
                </select>
                <input
                  type="text"
                  className="form-control border-2"
                  placeholder="Nhp tn sn phm bn mun tm kim"
                />
              </div>
              <button className="btn btn-primary mt-3 px-4 py-2" onClick={handleSearchProduct}>
                <i className="fa fa-search"></i> Tm kim sn phm
              </button>
            </div>
          </div>
        )}

        {/* Tra cu m vn n */}
        {activeTab === 'tracking' && (
          <div className="fil-panel">
            <div className="fil-form">
              <input
                type="text"
                className="form-control border-2 mb-3"
                placeholder="Nhp m vn n bn mun tm kim"
              />
              <button className="btn btn-primary mt-3 px-4 py-2" onClick={handleTrackOrder}>
                <i className="fa fa-search"></i> Tra cu m vn n
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default SearchForm;
