import React, { useState, useEffect } from "react";
import { Formik, Field, Form } from "formik";
import * as Yup from "yup";

const BannerManagement = () => {
  const [banners, setBanners] = useState([]);
  const [editingBanner, setEditingBanner] = useState(null); // Thm state  lu banner ang sa

  useEffect(() => {
    fetchBanners();
  }, []);

  const fetchBanners = async () => {
    try {
      const response = await fetch("http://14.225.29.33/api/banner/all");
      if (response.ok) {
        const data = await response.json();
        setBanners(data);
      } else {
        console.error("Failed to fetch banners:", response.statusText);
      }
    } catch (error) {
      console.error("Error fetching banners:", error);
    }
  };

  const handleDelete = async (id) => {
    try {
      const response = await fetch(`http://14.225.29.33/api/banner/delete/${id}`, {
        method: "DELETE",
      });

      if (response.ok) {
        setBanners(banners.filter((banner) => banner.id !== id)); // Xa banner khi danh sch
      } else {
        console.error("Error:", response.statusText);
      }
    } catch (error) {
      console.error("Error:", error);
    }
  };

  // Handle edit banner
  const handleEdit = (banner) => {
    setEditingBanner(banner); // Set banner cn sa
  };

  return (
    <div className="container my-5">
      {/* Banner List */}
      <h4>Danh sch Banner</h4>
      <table className="table table-striped">
        <thead>
          <tr>
            <th>Tiu </th>
            <th>M T</th>
            <th>Hnh nh</th>
            <th>Thao Tc</th>
          </tr>
        </thead>
        <tbody>
          {banners.map((banner) => (
            <tr key={banner.id}>
              <td>{banner.title}</td>
              <td>{banner.description}</td>
              <td>
                <img src={banner.imageUrl} alt={banner.title} width="100" />
              </td>
              <td>
                <button
                  className="btn btn-warning btn-sm"
                  onClick={() => handleEdit(banner)} // Nhn vo nt Sa
                >
                  Sa
                </button>
                <button
                  className="btn btn-danger btn-sm ms-2"
                  onClick={() => handleDelete(banner.id)} // Nhn vo nt Xa
                >
                  Xa
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* Add Banner Form */}
      {!editingBanner && (
        <>
          <h4>Thm Banner</h4>
          <AddBannerForm fetchBanners={fetchBanners} />
        </>
      )}

      {/* Edit Banner Form */}
      {editingBanner && (
        <>
          <h4>Sa Banner</h4>
          <EditBannerForm
            fetchBanners={fetchBanners}
            banner={editingBanner}
            setEditingBanner={setEditingBanner}
          />
        </>
      )}
    </div>
  );
};

const AddBannerForm = ({ fetchBanners }) => {
  const validationSchema = Yup.object({
    title: Yup.string().required("Tiu  l bt buc"),
    description: Yup.string().required("M t l bt buc"),
    image: Yup.mixed().required("Hnh nh l bt buc"),
  });

  const handleSubmit = async (values, { resetForm }) => {
    const formData = new FormData();
    formData.append("title", values.title);
    formData.append("description", values.description);
    formData.append("file", values.image);

    try {
      const response = await fetch("http://14.225.29.33/api/banner/add", {
        method: "POST",
        body: formData,
      });

      if (response.ok) {
        await fetchBanners(); // Reload li danh sch sau khi thm thnh cng
        resetForm();
      } else {
        console.error("Error:", response.statusText);
      }
    } catch (error) {
      console.error("Error:", error);
    }
  };

  return (
    <Formik
      initialValues={{ title: "", description: "", image: null }}
      validationSchema={validationSchema}
      onSubmit={handleSubmit}
    >
      {({ setFieldValue }) => (
        <Form>
          <div className="mb-3">
            <label className="form-label">Tiu </label>
            <Field
              type="text"
              name="title"
              className="form-control"
              placeholder="Nhp tiu "
            />
          </div>
          <div className="mb-3">
            <label className="form-label">M T</label>
            <Field
              as="textarea"
              name="description"
              className="form-control"
              placeholder="Nhp m t"
            />
          </div>
          <div className="mb-3">
            <label className="form-label">Hnh nh</label>
            <input
              type="file"
              className="form-control"
              onChange={(e) => {
                setFieldValue("image", e.target.files[0]);
              }}
            />
          </div>
          <button type="submit" className="btn btn-primary">
            Thm Banner
          </button>
        </Form>
      )}
    </Formik>
  );
};



const EditBannerForm = ({ fetchBanners, banner, setEditingBanner }) => {
  const validationSchema = Yup.object({
    title: Yup.string().required("Tiu  l bt buc"),
    description: Yup.string().required("M t l bt buc"),
    image: Yup.mixed().notRequired(),
  });

  const handleSubmit = async (values, { resetForm }) => {
    const formData = new FormData();
    formData.append("title", values.title);
    formData.append("description", values.description);

    // Ch thm nh mi vo formData nu ngi dng chn nh mi
    if (values.image) {
      formData.append("file", values.image);
    }

    try {
      const response = await fetch(
        `http://14.225.29.33/api/banner/update/${banner.id}`,
        {
          method: "PUT",
          body: formData,
        }
      );

      if (response.ok) {
        await fetchBanners(); // Reload li danh sch sau khi sa thnh cng
        setEditingBanner(null); // Reset state khi sa xong
        resetForm();
      } else {
        console.error("Error:", response.statusText);
      }
    } catch (error) {
      console.error("Error:", error);
    }
  };

  return (
    <Formik
      initialValues={{
        title: banner.title,
        description: banner.description,
        image: null, // Gi trng thi nh null nu khng c nh mi
      }}
      validationSchema={validationSchema}
      onSubmit={handleSubmit}
    >
      {({ setFieldValue, values }) => (
        <Form>
          <div className="mb-3">
            <label className="form-label">Tiu </label>
            <Field
              type="text"
              name="title"
              className="form-control"
              placeholder="Nhp tiu "
            />
          </div>
          <div className="mb-3">
            <label className="form-label">M T</label>
            <Field
              as="textarea"
              name="description"
              className="form-control"
              placeholder="Nhp m t"
            />
          </div>
          <div className="mb-3">
            <label className="form-label">Hnh nh</label>
            <input
              type="file"
              className="form-control"
              onChange={(e) => {
                setFieldValue("image", e.target.files[0]);
              }}
            />
            {/* Hin th nh c nu khng c nh mi */}
            {banner.imageUrl && !values.image && (
              <div className="mt-2">
                <img src={banner.imageUrl} alt="Current banner" width="100" />
              </div>
            )}
          </div>
          <button type="submit" className="btn btn-primary">
            Cp Nht Banner
          </button>
          <button
            type="button"
            className="btn btn-secondary ms-2"
            onClick={() => setEditingBanner(null)} // Hy sa
          >
            Hy
          </button>
        </Form>
      )}
    </Formik>
  );
};


export default BannerManagement;
